@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
* {
  font-family: "Roboto", sans-serif;
}

.theme-button {
  position: absolute;
  top: 80px;
  right: 20px;
}

.center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 120vh;
}
/*webkit-scrollbar does not work with Firefox, Firefox for Android, and Internet Explorer*/
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: white;
}

::-webkit-scrollbar-thumb {
  background-image: linear-gradient(rgb(71, 71, 253), rgb(128, 128, 255));
  border-radius: 10px;
}

